.cell {
    position: relative;
    height: 100%;
    width: 100%;
    justify-self: center;
    align-self: center;
    padding-left: 0.2rem;
    background-color: rgba(64, 70, 78, 255);
    border: 0.2rem solid rgba(32, 32, 32, 255);
    border-radius: 0.7rem;
    overflow-x: scroll;
    overflow-y: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.cell::-webkit-scrollbar {
    display: none;
}

.correct {
    font-size: 1.2rem;
    font-weight: bold;
}

.cell1 {
    grid-column: 1;
    grid-row: 1;
}

.cell2 {
    grid-column: 1;
    grid-row: 2;
}

.cell3 {
    grid-column: 1;
    grid-row: 3;
}

.cell4 {
    grid-column: 1;
    grid-row: 4;
}

.cell5 {
    grid-column: 2;
    grid-row: 1;
}

.cell6 {
    grid-column: 2;
    grid-row: 2;
}

.cell7 {
    grid-column: 2;
    grid-row: 3;
}

.cell8 {
    grid-column: 2;
    grid-row: 4;
}
